<template>
  <z-paging ref="paging" v-model="dataList" @query="onLoadList">
    <template #top>
      <SafeTopContainer>
        <view class="nav-bar">
          <view class="nav-left" @click="handleClickLeft">
            <wd-icon name="thin-arrow-left" color="#fff" size="22px"></wd-icon>
          </view>
          <view class="nav-title">MyOrders</view>
        </view>
        <!-- ======================= 顶部Tab栏 ======================= -->
        <view class="tab-bar">
          <view
            class="tab-item"
            :class="{ active_Requests: activeTab === 2 }"
            @click="handleChangeTab(2)"
          >
            Requests
          </view>
          <view
            class="tab-item"
            :class="{ active_Offerings: activeTab === 1 }"
            @click="handleChangeTab(1)"
          >
            Offerings
          </view>
        </view>
      </SafeTopContainer>
    </template>
    <view style="padding: 32rpx 0 0 0">
      <OrderCard
        v-for="item in dataList"
        :dataSource="item"
        :key="item.id"
        :type="activeTab"
        @click="handleClickItem"
      />
    </view>
    <template #bottom>
      <CustomTabbar />
    </template>
  </z-paging>
</template>

<script setup>
import SafeTopContainer from "@/components/SafeTopContainer.vue"
import { useNavigate, useSwitchTab } from "@/hooks/useNavigate"
import OrderCard from "@/components/OrderCard.vue"
import { useCommonState } from "@/store/useCommonState"
import CustomTabbar from "@/components/CustomTabbar.vue"

const paging = ref()
const dataList = ref([])

const commonState = useCommonState()

const onLoadList = async (page, size) => {
  const r = await indexOrders({
    page: page,
    rows: size,
    is_my: 1,
    methodtype: activeTab.value,
  })
  paging.value.complete(r.data.data)
}

const handleClickLeft = () => {
  // 跳转到上一页
  commonState.ORDER_SHOW_DETAIL_FROM_HOME = false
  useSwitchTab("/pages/home/index")
}

const activeTab = ref(2)

const handleChangeTab = (index) => {
  activeTab.value = index
  paging.value.reload()
}

const handleClickItem = (order) => {
  useNavigate("/pages/tracking/info", { id: order.id })
}

onShow(() => {
  paging.value?.reload()
})

onLoad(async (options) => {
  if (options.type) {
    activeTab.value = Number(options.type)
  }
})
</script>
